type IBUTTON_TYPES =
  | "EDIT"
  | "SAVE"
  | "CANCEL"
  | "MENU"
  | "CLOSE_GRAY"
  | "OCR"
  | "DELETE"
  | "DOWNLOAD"
  | "VIEW"
  | "SORT"
  | "SORT_ASC"
  | "SORT_DESC"
  | "DEDUPE_SEARCH";

interface IButtonProps extends ButtonProps, IBaseButtonCommonProps {
  buttonType?: IBUTTON_TYPES;
  className?: string;
  disabled?: boolean;
  isDisabledByAcl?: boolean;
  onClick?: (e: any) => void;
  onClickWithLoader?: () => any;
  iconClassName?: string;
  hideDefaultIconClassName?: string;
}

const IconButton = (props: IButtonProps) => {
  const {
    buttonType,
    className,
    // icon,
    disabled,
    isDisabledByAcl,
    onClick,
    onClickWithLoader,
    hideDefaultIconClassName,
    isIgnoreAccessLevel,
    ...primeReactProps
  } = props;

  const [showLoader, setShowLoader] = useState(false);

  const onHandlerClick = async (e: any) => {
    if (onClick) {
      onClick(e);
    } else if (onClickWithLoader) {
      const res = onClickWithLoader();
      setShowLoader(true);
      if (res && Promise.resolve(res) === res) {
        res
          .then((message: any) => {
            setShowLoader(false);
          })
          .catch(() => {
            setShowLoader(false);
          });
      }
    }
  };
  const { icon, iconClassName, disabledIcon, tooltipText } = BUTTON_TYPE[buttonType ?? "EDIT"];
  let disabledButton = disabled;
  if (!isIgnoreAccessLevel) {
    if (isDisabledByAcl) {
      disabledButton = true;
    }
  }
  return (
    <Button
      {...primeReactProps}
      disabled={disabledButton}
      className={` ${className ?? ""} ${props.children ? "" : "w-6 h-6"}`}
      onClick={onHandlerClick}
      tooltip={tooltipText}
      tooltipOptions={
        props.tooltipOptions
          ? props.tooltipOptions
          : {
              position: "top",
              className: "text-xs font-Roboto bg-black text-white !p-1 rounded-md ",
            }
      }
    >
      {props.children}
      {showLoader ? (
        <img src={LoadingIcon} alt="loading" className="w-5 h-5 animate-spin" />
      ) : (
        <img
          src={disabledButton ? disabledIcon : icon}
          className={`${hideDefaultIconClassName ? "" : " w-3.5 h-3.5"} ${iconClassName}`}
          alt="Sahayya Logo"
          width={24}
        />
      )}
    </Button>
  );
};

export default withAuthButton(IconButton);
